.wcb-testimonials__wrap {
	.slick-dots {
		position: relative;
		bottom: auto;
	}
	.slick-dots li {
		margin: 0px;
	}
	.slick-dots li button:before {
		font-size: 8px;
	}

	.slick-prev,
	.slick-next {
		color: black;
		padding: 0.5rem;
		width: unset;
		height: unset;
		&:hover {
			color: black;
		}
		&::before {
			content: "";
			display: none;
		}
	}
}

.wcb-testimonials__item {
	display: flex;
	position: relative;
}

.wcb-testimonials__item-image {
	display: inline-flex;
	align-items: flex-start;
	flex-shrink: 0;
}
.wcb-testimonials__item-user {
	display: inline-flex;
	align-items: center;
}
